body{
  overflow-x: hidden;
}
.t-n7px{
  top: -7px !important;
}

.r-5px{
  right: 5px !important;
}
.m-h-500px {
  min-height: 500px;
}
.login-container {
  position: absolute !important;
  top: 50% !important;
  left: 50% !important;
  transform: translate(-50%, -50%) !important;
}
.chat_center{
  width: 300px;
  margin: 30px auto;
  margin-bottom: 10px;
  text-align: center;
} 
.attr_chat{
  width: 100px;
}
.centerItem{
  position: absolute !important;
  top: 50% !important;
  left: 50% !important;
  transform: translate(-50%, -50%) !important;
}
.loader {
  border: 5px solid #f3f3f3;
  border-radius: 50%;
  border-top: 5px solid #3498db;
  width: 70px;
  height: 70px;
  -webkit-animation: spin 2s linear infinite; /* Safari */
  animation: spin 2s linear infinite;
  position: absolute;
  top: Calc(50% - 86px);
  left: 50%;
}
/* Safari */
@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.popUpContainer {
  display: block;
  position: fixed;
  z-index: 1050;
  left: 0;
  top: 0px;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(37, 34, 34, 0.5);
}


.chart {
  font-family: Arial, sans-serif;
  font-size: 12px;
}

.axis path,
.axis line {
  fill: none;
  stroke: #000;
  shape-rendering: crispEdges;
}

.bar {
  fill: url(#blue);
}

.bar-failed {
  fill: url(#red);
}

.bar-running {
  fill: url(#green);
}

.bar-succeeded {
  fill: url(#blue);
}

.bar-killed {
  fill: url(#orange);
}

#forkme_banner {
  display: block;
  position: absolute;
  top: 0;
  right: 10px;
  z-index: 10;
  padding: 10px 50px 10px 10px;
  color: #fff;
  background: url("http://dk8996.github.io/Gantt-Chart/images/blacktocat.png")
    #0090ff no-repeat 95% 50%;
  font-weight: 700;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  border-bottom-left-radius: 2px;
  border-bottom-right-radius: 2px;
  text-decoration: none;
}

rect {
  opacity: 0.9;
}

text {
  font-weight: bold;
}

div.tooltip {
  /* text-align: center; */
  display: table-cell;
  vertical-align: middle;
  color: white;
  position: absolute;
  text-align: center;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  padding: 10px;
  font: 12px sans-serif;
  /* background: lightsteelblue;	 */
  background: #283048; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #859398,
    #283048
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  border: 0px;
  border-radius: 5px;
  pointer-events: none;
}
